<template>
    <div id="app">
        <div class="scroll-content J_scrollContent">
            <div class="chatContent" id="chatContent" >
                
                
              <div v-for="value in arr">
                
                <div class="clearfix">
                    <div class="message others">
                        <div class="avatar" data-author-id="lj"> <img src="./assets/tu20468_3.jpg" alt=""> </div>
                        <div class="content">
                            <p class="author_name">{{name1}}</p>
                            <div class="bubble  bubble_default left">
                                <div class="bubble_cont">
                                    <div class="plain"> <pre>{{value.text1}}</pre> </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                <div class="clearfix">
                    <div class="message me">
                        <div class="avatar" data-author-id="me"> <img src="./assets/a-default.png" alt=""> </div>
                        <div class="content">
                            <p class="author_name">{{name2}}</p>
                            <div class="bubble  bubble_primary right">
                                <div class="bubble_cont">
                                    <div class="plain"> <pre>{{value.text2}}</pre> </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
               
             </div>

            </div>
        </div>
    </div>
</template>

<script>
 
export default {
    name: 'app',
    data() {
        return {
          "name1":"主管",
          "name2":"主管基友",
           arr:[],
           arr1:[{text1:"我们用 v-for 指令根据一组数组的选项列表进行渲染",text2:"说的什么？"},{text1:"v-for 指令需要以 item in items 形式的特殊语法， items 是源数据数组并且 item 是数组元素迭代的别名。",text2:"在 v-for 块中，我们拥有对父作用域属性的完全访问权限。"},{text1:" v-for 还支持一个可选的第二个参数为当前项的索引",text2:"如同 v-if 模板，你也可以用带有 v-for 的 <template> 标签来渲染多个元素块。例如："}]     
        }
    },    
    mounted (){
     for (var i=0,n=this.arr1.length; i<n; i++) {
        (function(x,y,z){
           setTimeout(function(){
            y=y.push(z[x])
          },x*2000)
        })(i,this.arr,this.arr1)
     };

    }

}
</script>



